import React, { useEffect } from "react";
import { Link } from "react-router-dom";
import { ReactComponent as FacebookIcon } from "../../assets/icons/facebook_icon.svg";
import { ReactComponent as GithubIcon } from "../../assets/icons/github_icon.svg";
import { ReactComponent as LinkedinIcon } from "../../assets/icons/linkedin_icon.svg";
import { ReactComponent as MailIcon } from "../../assets/icons/mail_icon.svg";
import { ReactComponent as PhoneIcon } from "../../assets/icons/phone_icon.svg";
import { ReactComponent as TelegramIcon } from "../../assets/icons/telegram_icon.svg";
import { DownloadCV, WatchPortfolio } from "./button";
import s from "./style.module.scss";

const titles = ["FULL STACK", "FRONT-END", "AI"];

const letters = ["A", "C", "D", "E", "F", "G", "H", "I", "J"];

const Home = () => {
  const [currentText, setCurrentText] = React.useState("");
  const [currentTextIndex, setCurrentTextIndex] = React.useState(0);
  const [step, setStep] = React.useState(0);
  const [currentTitle, setCurrentTitle] = React.useState(titles[0]);

  useEffect(() => {
    const interval = setInterval(() => {
      setStep((prevStep) => prevStep + 1);
    }, 50);

    return () => clearInterval(interval);
  }, []);

  useEffect(() => {
    if (step < currentTitle.length * 10) {
      const titleIndex = Math.floor(step / 10);
      const currentLetter = currentTitle[titleIndex];
      const randomLetter = letters[Math.floor(Math.random() * letters.length)];
      setCurrentText((prevText) => {
        const newText = prevText.split("");
        newText[titleIndex] = randomLetter;
        return newText.join("");
      });

      if (step % 10 === 9) {
        setCurrentText((prevText) => {
          const newText = prevText.split("");
          newText[titleIndex] = currentLetter;
          return newText.join("");
        });
      }
    } else {
      setTimeout(() => {
        setCurrentText("");
        setStep(0);
        setCurrentTextIndex((prevIndex) => (prevIndex + 1) % titles.length);
        setCurrentTitle(titles[(currentTextIndex + 1) % titles.length]);
      }, 1000);
    }
  }, [step]);

  return (
    <div className={s.container}>
      <header>
        <ul>
          <li title="Facebook">
            <Link
              to="https://www.facebook.com/levonyanfahrad20"
              target="_blank"
            >
              <FacebookIcon />
            </Link>
          </li>
          <li title="Linkedin">
            <Link
              to="https://www.linkedin.com/in/levonyanfahrad/"
              target="_blank"
            >
              <LinkedinIcon />
            </Link>
          </li>
          <li title="Github">
            <Link to="https://github.com/Fahrad20" target="_blank">
              <GithubIcon />
            </Link>
          </li>
          <li title="Telegram">
            <Link to="tg://resolve?domain=levonyan_20" target="_blank">
              <TelegramIcon />
            </Link>
          </li>
          <li title="Gmail">
            <Link to="mailto: levonyanfahrad20@gmail.com">
              <MailIcon />
            </Link>
          </li>
          <li title="Phone number">
            <Link to="tel: +37433999609">
              <PhoneIcon />
            </Link>
          </li>
        </ul>
      </header>
      <h1>{currentText}</h1>
      <DownloadCV />
      <WatchPortfolio />
    </div>
  );
};

export default Home;
